@import url('https://fonts.googleapis.com/css2?family=Rubik+Moonrocks&family=Special+Elite&display=swap');
body {
  margin: 0;
  font-family: 'Special Elite', cursive, 'Impact', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
